import { useState } from 'react';

import { TableBody } from './TableBody';

import {
  Container,
  ButtonsContainer,
  Button,
  ButtonItemGreen,
  ButtonItemRed,
  ButtonItemViolet,
  TableHero,
  MyPaginate,
} from './Table.styled';

export default function Table({ dataTable }) {
  //////////Pagination/////////////////////////////

  // Here we use item offsets; we could also use page offsets
  // following the API or data you're working with.
  const [itemOffset, setItemOffset] = useState(0);
  const itemsPerPage = 15; //    15 () 

  // Simulate fetching items from another resources.
  // (This could be items from props; or items loaded in a local state
  // from an API endpoint with useEffect and useState)
  const endOffset = itemOffset + itemsPerPage;
  console.log(`Loading items from ${itemOffset} to ${endOffset}`);
  const currentItems = dataTable.slice(itemOffset, endOffset);
  const pageCount = Math.ceil(dataTable.length / itemsPerPage);

  // Invoke when user click to request another page.
  const handlePageClick = event => {
    const newOffset = (event.selected * itemsPerPage) % dataTable.length;
    console.log(`User requested page number ${event.selected}, which is offset ${newOffset}`);
    setItemOffset(newOffset);
  };

  //       -
  const conducted = '';
  const deleteRow = '';
  const recorded = '';
  const conductedFilter = dataTable.filter(el => el.status === conducted).length;
  const deleteFilter = dataTable.filter(el => el.status === deleteRow).length;
  const recordedFilter = dataTable.filter(el => el.status === recorded).length;

  const arr = dataTable.map(el => {
    let conductedFilter = [];
    let deleteFilter = [];
    let recordedFilter = [];
    if (el.status === conducted) {
      conductedFilter = [el.status];
    }
    if (el.status === deleteRow) {
      deleteFilter.push(el.status);
    }
    if (el.status === recorded) {
      recordedFilter = [...recordedFilter, el.status];
    }

    return {
      conductedArray: conductedFilter,
      deleteArray: deleteFilter,
      recordedArray: recordedFilter,
    };
  });
  /* .flatMap(item => item); */

  console.log('arr,', arr);

  return (
    <Container>
      <ButtonsContainer>
        <Button type="button">
          : <ButtonItemGreen> {conductedFilter} .</ButtonItemGreen>
        </Button>
        <Button type="button">
           : <ButtonItemRed> {deleteFilter} .</ButtonItemRed>
        </Button>
        <Button type="button">
          : <ButtonItemViolet> {recordedFilter} .</ButtonItemViolet>
        </Button>
      </ButtonsContainer>
      <TableHero>
        <TableBody data={currentItems} />
      </TableHero>
      <MyPaginate
        breakLabel="..."
        nextLabel=" >"
        onPageChange={handlePageClick}
        pageRangeDisplayed={5}
        pageCount={pageCount}
        previousLabel="< "
        renderOnZeroPageCount={null}
        pageLinkClassName="page-link"
      />
    </Container>
  );
}
