import { useState, useEffect } from 'react';

import SideBar from '../components/SideBar/SideBar';
import Table from '../components/Table/Table';

/* import { Form } from '../components/Form/Form';
import MatrixTable from '../components/TableMatrix/MartixTable'; */

import { fetchAPI } from '../services/isroAPI';

import { Container } from './HomePage.styled';

export default function HomePage() {
  const [dataTable, setDataTable] = useState([]);

  useEffect(() => {
    fetchAPI().then(result => setDataTable(result.customer_satellites));
  }, []);

  //    26-06-2022  26.06.22
  dataTable.map(item => {
    item.launch_date = item.launch_date.replace('-', '.').replace('-', '.').slice(0, 6) + item.launch_date.slice(7, item.launch_date.length);

    return item;
  });

  console.log(dataTable);
  return (
    <Container>
      <SideBar />
      {/* <Form /> */}
      <Table dataTable={dataTable} />
      {/* {data.row && <MatrixTable />} */}
    </Container>
  );
}
